<template>
  <input ref="peerLink" :value="peerLink" readonly>
  <button @click="copyPeerLink">Copy</button>
  <video ref="mainView" class="main-view"></video>
  <video ref="selfView" muted class="self-view"></video>
</template>

<script>
import Peer from "peerjs";

let selfViewStream = null;

export default {
  name: "App",
  data() {
    return { peer: null, peerId: null };
  },
  
  computed: {
    peerLink() {
      if (!this.peerId) return "";
      return location.origin + location.pathname + "?p=" + this.peerId;
    }
  },

  methods: {
    copyPeerLink() {
      this.$refs.peerLink.focus();
      this.$refs.peerLink.select();
      document.execCommand("copy");
    }
  },

  mounted() {
    this.$refs.mainView.onloadedmetadata = () => this.$refs.mainView.play();
    this.$refs.selfView.onloadedmetadata = () => this.$refs.selfView.play();
    
    const bindMainView = (call) => {
      console.log(call);
      call.on("stream", (mediaStream) => {
        this.$refs.mainView.srcObject = mediaStream;
      });
    };

    this.peer = new Peer();
    this.peer.on("open", id => (this.peerId = id));
    this.peer.on("call", (call) => {
      console.log("Called from peer " + call.peer);
      call.answer(selfViewStream);
      bindMainView(call);
    });
    this.peer.on("error", err => console.log(err));

    navigator.mediaDevices.getUserMedia({ audio: true, video: true })
    .then((mediaStream) => {
      selfViewStream = mediaStream;
      this.$refs.selfView.srcObject = mediaStream;

      const peerIdMatch = location.search.match(/p=([\da-f-]*)/);
      if (peerIdMatch) {
        const friendPeerId = peerIdMatch[1];
        console.log("Calling peer " + friendPeerId);
        this.peer.on("open", () => {
          bindMainView(this.peer.call(friendPeerId, mediaStream));
        });
      }
    });
  }
};
</script>

<style>
body {
  margin: 0;
}

input {
  font-size: 16px;
  padding: 8px;
}

.main-view {
  position: fixed;
  top: 0;
  left: 0;
  max-width: 100vw;
  max-height: 100vh;
  z-index: -1;
}

.self-view {
  position: fixed;
  top: 0;
  right: 0;
  max-width: 50vw;
  max-height: 30vh;
  transform: rotateY(180deg);
}
</style>
